// router.js
// Bu dosya, uygulamanın rotalarını ve her yol için yüklenecek bileşenleri tanımlar ve router'ı dışa aktarır

import { createRouter, createWebHistory } from 'vue-router';
import VideoUpload from '../components/VideoUpload.vue'; // Video yükleme sayfası bileşeni
import VideoPlayback from '../components/VideoPlayback.vue'; // Video oynatma sayfası bileşeni

// Uygulamanın rotalarını tanımlama
const routes = [
  { path: '/', component: VideoUpload }, // Kullanıcıların video yüklediği ana sayfa
  { path: '/playback', component: VideoPlayback }, // Video oynatma sayfası
];

// History modunda ve tanımlanan rotalarla bir router örneği oluşturma
const router = createRouter({
  history: createWebHistory(), // Temiz URL'ler için HTML5 history modunu kullanma
  routes, // Tanımlanan rotaları ayarlama
});

export default router; // Router örneğini Vue uygulamasında kullanılmak üzere dışa aktarma