// store.js
import { createStore } from 'vuex';

const store = createStore({
  state: {
    videoFilename: '', // Yüklenen video dosyasının adı
    dataFilename: '', // Oluşturulan yoğunluk verisi dosyasının adı
  },
  mutations: {
    setVideoFilename(state, filename) {
      state.videoFilename = filename;
    },
    setDataFilename(state, filename) {
      state.dataFilename = filename;
    },
  },
  actions: {
    // Şu an için eylemlere gerek yok
  },
  getters: {
    getVideoFilename: (state) => state.videoFilename,
    getDataFilename: (state) => state.dataFilename,
  },
});

export default store;